
.vux-demo {
  text-align: center;
  margin-bottom: 500px;
}
.weui-tabbar {
  position: fixed!important;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.logo {
  width: 100px;
  height: 100px;
}
.child-view {
  position: absolute;
  width: 100%;
  overflow: auto;
  /*padding-bottom:55px;*/
  /*padding-top:45px;*/
  top: 45px;
  bottom: 52px;
  -webkit-transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
}
.forward-enter,
.reverse-leave-active {
  opacity: 0;
  -webkit-transform: translate(50%, 0);
  transform: translate(50%, 0);
}
.forward-leave-active,
.reverse-enter {
  opacity: 0;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
.vux .vux-header {
  position: fixed!important;
  width: 100%;
  z-index: 100;
  background-color: #3190e8;
}
body,
html {
  height: 100%;
  width: 100%;
  background-color: #f5f5f5;
}
.vux-header-right i {
  font-style: normal;
  color: #ffffff;
  font-size: 16px;
}
.weui-bar__item_on .weui-tabbar__icon i {
  color: #3190e8!important;
}
.weui-bar__item_on .weui-tabbar__label span {
  color: #3190e8!important;
}
.vux-header .vux-header-left .left-arrow:before {
  border: 1px solid #fff;
}
i {
  font-style: normal;
}



