
.borderimg {
  width: 200px;
  height: 200px;
  border: 1px solid;
  margin: 20px;
  -webkit-border-image: -webkit-linear-gradient(blue 10%, #ccc 50%) 30 31;
          border-image: -webkit-linear-gradient(blue 10%, #ccc 50%) 30 31;
}
.childhello3 {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.slidelist-enter {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
.slidelist-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}



