html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
.smallball {
  -webkit-transition: -webkit-transform 1s cubic-bezier(0.3, -0.6, 0.7, -0.15);
  transition: -webkit-transform 1s cubic-bezier(0.3, -0.6, 0.7, -0.15);
  transition: transform 1s cubic-bezier(0.3, -0.6, 0.7, -0.15);
  transition: transform 1s cubic-bezier(0.3, -0.6, 0.7, -0.15), -webkit-transform 1s cubic-bezier(0.3, -0.6, 0.7, -0.15);
  position: fixed;
  z-index: 99;
}
.smallball .item {
  width: 4vw;
  height: 4vw;
  border-radius: 50%;
  background: deepskyblue;
  -webkit-transition: -webkit-transform 1s linear;
  transition: -webkit-transform 1s linear;
  transition: transform 1s linear;
  transition: transform 1s linear, -webkit-transform 1s linear;
}
body,
html {
  background-color: #fbf9fe;
  width: 100%;
}
#app {
  width: 100%;
  height: 100%;
}
.childPages {
  bottom: 0!important;
}
.clearfix:after {
  content: " ";
  display: block;
  clear: both;
  height: 0;
}
.fl {
  float: left;
}
.fr {
  float: right;
}



