
nav {
  background: white;
  margin-bottom: 15px;
}
.vux-slider .vux-indicator-right {
  right: 49%!important;
}
.swipehead .imgwrap {
  width: 25vw;
  padding: 2vw 0;
  float: left;
  height: 40%;
  text-align: center;
  color: #666;
}
.swipehead .imgwrap img {
  width: 56%;
}
.scrollerwrap {
  position: absolute;
  top: 0vw;
  width: 100%;
  bottom: 0;
}
.itemList {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  border-bottom: 1px solid #f1f1f1;
  padding: 20px 10px;
}
.itemList .left img {
  width: 20vw;
  height: 20vw;
  margin-right: 2vw;
}
.itemList .right {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
.itemList .right h4 {
  float: left;
  margin: 0;
  font-size: 4vw;
}
.itemList .right h4:before {
  content: '\54C1\724C';
  display: inline-block;
  font-size: 2vw;
  line-height: 1rem;
  color: #333;
  background-color: #ffd930;
  padding: 0 1vw;
  border-radius: 0.5vw;
  margin-right: 1vw;
}
.itemList .right .supports {
  float: right;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  font-size: 3vw;
}
.itemList .right .supports li {
  float: left;
  list-style-type: none;
  margin-right: 8px;
  color: #999;
}
.itemList .right .supports li:last-child {
  margin-right: 0;
}
.itemList .right .rmid {
  margin-top: 3vw;
}
.itemList .right .rmid .rantings {
  float: left;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.itemList .right .rmid .kuaidi {
  float: right;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: auto;
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  margin-right: -2vw;
}
.itemList .right .rmid .kuaidi_left {
  font-size: 3vw;
  padding: 2px;
  border-radius: 1vw;
  margin-right: 0.5vw;
  color: #fff;
  background: #3190e8;
  border: 1px solid #3190e8;
}
.itemList .right .rmid .kuaidi_right {
  font-size: 3vw;
  padding: 2px;
  border-radius: 1vw;
  color: #3190e8;
  border: 1px solid #3190e8;
}
.itemList .rbot {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 10px;
  margin-top: 2vw;
}



